.card_profile-info {
  margin: 2rem;

  &-contact,
  &-about {
    margin-bottom: 1rem;
    h2 {
      font-size: 0.8rem;
      color: $gray-lightest;
      margin-bottom: 0.2rem;
      letter-spacing: 0.1rem;
      text-transform: uppercase;
    }

    p {
      all: unset;
      font-size: 0.8rem;
      font-weight: bold;
      letter-spacing: 0.03rem;
      margin-bottom: 0.2rem;
      line-height: 1.3rem;
    }
  }
}

@media screen and (max-width: 425px) {
  .card_profile-info {
    &-contact,
    &-about {
      h2 {
        font-size: 0.7rem;
      }

      p {
        font-size: 0.7rem;
      }
    }
  }
}
